  }
  .topNav .left a {
    display: block;
    padding: 0;
  }
  .surahDetailContainer .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .surahDetailMain {
    padding: 3rem;
  }
  .ayat {
    padding: 2rem;
  }
  .menuAndNavDraw button {
    display: none;
  }
  button.hamburger {
    display: none;
  }
  .ayatHead h3 {
    font-size: 1.3rem;
  }
  .ayatFooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  audio {
    width: 300px;
    transform: scale(0.9);
    -webkit-transform: scale(0.9);
    -moz-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
  }
}
.show {
  max-height: 1000px !important;
}
/* Colors */
.audioPlaying {
  background-color: rgba(218, 165, 32, 0.2);
}
.audioPlaying:hover {
  background-color: rgba(218, 165, 32, 0.3);
}
.yellow {
  color: rgb(191, 131, 20);
}
.gold {
  color: goldenrod;
